import React, { Component } from 'react';
import './App.css';
import ScreenContext from './ScreenContext';
import Funlinks from './Funlinks';
import btn_icon_back_fun_links from './images/btn_icon_back_fun_links.png';

// UI framework component imports
import Appbar from 'muicss/lib/react/appbar';

export default class Fun_linksScreen extends Component {

  static contextType = ScreenContext;


  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
    {
      let dataSheet = this.context.appActions.dataSheets['fun'];
      let serviceOptions = this.context.appActions.serviceOptions_fun;
      if ( !this.context.appActions.dataSheetLoaded['fun']) {
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("", this.context.appActions.dataSlots);
        this.context.appActions.loadData_jsonsrc3(dataSheet, serviceOptions, true);
        this.context.appActions.dataSheetLoaded['fun'] = true;
      }
      this._dataSheetLoadTimer_fun = setInterval(() => {  // Reload data regularly
        serviceOptions.servicePath = dataSheet.expandSlotTemplateString("", this.context.appActions.dataSlots);
        this.context.appActions.loadData_jsonsrc3(dataSheet, serviceOptions, false);
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this._dataSheetLoadTimer_fun);
  }

  componentDidUpdate(prevProps, prevState) {
  }

  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.context.transitionId && this.context.transitionId.length > 0 && this.context.atTopOfScreenStack && this.context.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.context.transitionId;
    }
    if ( !this.context.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    // eslint-disable-next-line
    const dataSheet_fun = this.context.dataSheets['fun'];
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      boxSizing: 'border-box',
      backgroundColor: '#f6f6f6',
     };
    
    // Source items and any special components used for list/grid element 'list'.
    let items_list = [];
    let listComps_list = {};
    items_list = items_list.concat(this.context.appActions.getDataSheet('fun').items);
    this._elList_items = [];
    
    const style_elList = {
      height: 'auto',  // This element is in scroll flow
     };
    
    return (
      <div className="AppScreen Fun_linksScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elSpacer">
            <div />
          </div>
          
          <div className="hasNestedComps elList">
            <ul style={style_elList}>
              {items_list.map((row, index) => {
                let itemComp = (row._componentId)
                    ? listComps_list[row._componentId]
                    : <Funlinks dataSheetId={'fun'} dataSheetRow={row} {...{ 'Link': row['Link'], 'Title': row['Title'], 'Desc': row['Desc'], }} ref={(el) => {if (el) this._elList_items.push(el)}} />;
                return (<li key={row.key || index}>
                    {itemComp}
                  </li>);
              })}
              <div className="marker" ref={(el)=> this._elList_endMarker = el} />
            </ul>
          </div>
        </div>
        <div className="navBarContainer">
         <Appbar className="navBar">
          <div className="title">Fun Activities</div>  <div className="backBtn" onClick={ (ev)=>{ this.context.appActions.goBack() } }><img src={btn_icon_back_fun_links} alt="" style={{width: '50%'}} /> </div>
         </Appbar>
        </div>
        
      </div>
    )
  }
  
}
